/* ==========================================================================
   IMPORT & FONT SETTINGS (if not already imported globally)
   ========================================================================== */
/* (If these imports are not global in your project, uncomment them)
@import url('https://fonts.googleapis.com/css2?family=Merriweather:ital,wght@0,300;0,400;0,700;0,900;1,300;1,400;1,700;1,900&display=swap');
*/

/* ==========================================================================
   CSS VARIABLES
   ========================================================================== */
   :root {
    /* Colors */
    --base-bg: #fff;
    --base-text: #000;
    --accent-green: lightgreen;
    --accent-red: crimson;
    --lighter-bg: #fefefe;
    --lighter-bg2: #f8f9fa;
    --container-bg: #FDFFC2;
    --container-border: var(--border-color, #2c2c2c);
    --table-header-bg: #FF76CE;
    --table-cell-bg: #FDFFC2;
    --table-even-bg: var(--lesson-accent3, #f3f3f3);
    --navigation-disabled-bg: #cf0a0a;
    --hint-bg: #FDFFC2;
    --hint-border: #FF76CE;
    --task-bg: #fcd535;
    --task-stripe: #000;
    --grammar-box-bg: #94FFD8;
    --grammar-box-alt: #A3D8FF;
    --lp-bg: linear-gradient(135deg, #fcd535, #ffe57e, #fff9b0);
    --lp-border: rgba(255, 229, 126, 0.5);
    --helperz-bg: var(--primary-bg, #000);
    --helperz-text: var(--primary-text, #fff);
  
    /* Sizing & Misc */
    --transition-duration: 0.3s;
    --transition-duration-long: 0.5s;
    --border-radius: 12px;
  }
  
  /* ==========================================================================
     GLOBAL & BODY STYLES
     ========================================================================== */
  body {
    font-family: 'Merriweather', serif;
    background-color: var(--base-bg);
    color: var(--base-text);
    margin: 0;
    padding: 0;
  }
  
  /* Utility classes for feedback */
  .correct {
    background-color: var(--accent-green) !important;
  }
  .incorrect {
    background-color: var(--accent-red) !important;
  }
  
  /* ==========================================================================
     MAIN CONTENT CONTAINER (Mane) - Multi-Column Magazine Style
     ========================================================================== */
  .mane {
    column-count: 2;
    height: 100%;
    box-sizing: border-box;
    overflow-y: auto;
    margin-bottom: 5rem;
    background: linear-gradient(to right, var(--lighter-bg), var(--lighter-bg2));
    border-radius: 15px;
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.1);
  }
  
  /* Children in .mane: cards/sections */
  .mane > * {
    padding: 1rem;
  }
  .mane > *:hover {
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
  }
  
  /* Text inside .mane */
  .mane h2, .mane h3 {
    color: #333;
    margin-bottom: 15px;
  }
  .mane p {
    font-size: 1rem;
    color: #555;
    margin-bottom: 15px;
    text-align: justify;
  }
  
  /* Images in .mane */
  .mane img {
    padding: 0.3rem;
    margin: 0.5rem 0;
    max-width: 100%;
    height: auto;
    object-fit: cover;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  .mane img + figcaption {
    text-align: center;
    font-size: 0.9rem;
    color: #666;
    margin-top: 0.5rem;
    font-style: italic;
  }
  
  /* ==========================================================================
     GRID & NAVIGATION STYLES FOR LESSON PAGE
     ========================================================================== */
  .lesson-page {
    display: grid;
    grid-template-columns: 5% auto 5%;
    grid-template-rows: auto;
    gap: 10px;
    overflow: hidden;
    position: absolute;
    background-color: var(--lighter-bg2);
    width: 100%;
    min-height: 79vh;
  }
  .lesson-page .content {
    grid-column: 2 / 3;
    overflow: auto;
    padding: 20px;
    height: 100%;
    box-sizing: border-box;
  }
  .lesson-page .content > div {
    max-width: 100%;
    word-wrap: break-word;
    margin: 0;
  }
  
  /* Navigation Buttons & Icons */
  .navigationL, 
  .navigationR {
    padding: 0.2rem;
    cursor: pointer;
  }
  .navigationL { grid-column: 1; }
  .navigationR { grid-column: 3; }
  .disabled {
    cursor: not-allowed;
    opacity: 0.6;
    transition: all var(--transition-duration) ease;
  }
  .disabled:hover {
    color: #aaa;
    border-color: #ccc;
  }
  .navIcon-pos {
    position: fixed;
    transform: translateY(30vh);
    width: 4%;
    opacity: 0.3;
  }
  .nav-dir {
    text-align: center;
    font-size: 0.8em;
    font-weight: 900;
    color: whitesmoke;
    text-shadow: -1px -1px 0 #000, 0 -1px 0 #000, 1px -1px 0 #000,
                 1px 0 0 #000, 1px 1px 0 #000, 0 1px 0 #000,
                -1px 1px 0 #000, -1px 0 0 #000;
  }
  .navigation:disabled {
    background: var(--navigation-disabled-bg);
    cursor: not-allowed;
  }
  
  /* Headings inside .mane override */
  .mane > h1, .mane h2, .mane h3, .mane h4, .mane h5, .mane h6 {
    color: var(--base-text) !important;
    margin-bottom: 20px;
  }
  
  /* ==========================================================================
     CONTAINERS & PARTS
     ========================================================================== */
  .container {
    background-color: var(--container-bg);
    border: 1px solid var(--container-border);
    padding: 20px;
    margin-bottom: 20px;
    border-radius: 8px;
  }
  .part {
    margin-bottom: 20px;
    border-radius: 8px;
  }
  .part > table {
    background-color: #F6F0BC;
  }
  .bunch {
    background-color: #FF76CE;
    color: #fff;
    border-radius: 10px;
    padding: 1rem;
    box-shadow: 0 8px 15px rgba(0, 0, 0, 0.1);
    margin: 20px auto;
    max-width: 600px;
    transition: all var(--transition-duration) ease;
    line-height: 1.8;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.9);
  }
  .bunch:hover {
    transform: scale(1.05);
    border: 2px solid black;
  }
  .hint {
    background-color: var(--hint-bg);
    color: #181a20;
    padding: 10px;
    margin-bottom: 15px;
    border-left: 5px solid var(--hint-border);
    border-radius: 4px;
    font-style: italic;
  }
  
  /* ==========================================================================
     EXPANDABLE & TOOLTIP ELEMENTS
     ========================================================================== */
  .expandable {
    position: relative;
    display: inline-block;
    cursor: pointer;
  }
  .expandable .word {
    font-weight: bold;
    text-decoration: underline;
    cursor: pointer;
  }
  .expandable .definition {
    display: none;
    position: absolute;
    background-color: #FF76CE;
    color: #fff;
    padding: 10px;
    border-radius: 4px;
    z-index: 1;
    width: max-content;
    max-width: 300px;
    top: 100%;
    left: 50%;
    transform: translateX(-50%);
  }
  .expandable:hover .definition {
    display: block;
  }
  
  /* ==========================================================================
     LISTS & TABLES
     ========================================================================== */
  /* Lists */
  ol, ul {
    margin-bottom: 20px;
    padding-left: 2rem;
  }
  li {
    margin: 1rem;
    font-size: 16px;
    line-height: 1.5;
  }
  li > div {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-evenly;
  }
  li > div > button {
    min-width: 100px;
    width: auto;
    max-width: 100%;
    cursor: pointer;
    text-align: center;
    box-sizing: border-box;
    transition: background-color var(--transition-duration) ease, width var(--transition-duration) ease;
  }
  
  /* Tables */
  table {
    width: 100%;
    border-collapse: collapse;
    margin-bottom: 20px;
    background-color: var(--lesson-bg, #A3D8FF);
    color: var(--lesson-text, #000);
    border-radius: 8px;
    overflow: hidden;
  }
  table, th, td {
    border: 1px solid var(--lesson-border, #ccc);
  }
  th, td {
    padding: 15px;
    text-align: left;
  }
  th {
    background-color: var(--lesson-accent1, #FF76CE);
    color: var(--lesson-text, #fff);
  }
  td {
    background-color: var(--lesson-accent2, #FDFFC2);
  }
  tr:nth-child(even) td {
    background-color: var(--lesson-accent3, #f3f3f3);
  }
  
  /* ==========================================================================
     LESSON IMAGE & CONTENT
     ========================================================================== */
  .lesson-image {
    width: 100%;
    height: auto;
  }
  .lesson-content {
    margin-top: 20px;
  }
  
  /* ==========================================================================
     LESSON OVERLAY (e.g., expanded data view)
     ========================================================================== */
  .lesson-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.8);
    color: white;
    padding: 20px;
    box-sizing: border-box;
    overflow: auto;
    z-index: 1000;
  }
  .lesson-overlay img {
    max-width: 100%;
    height: auto;
  }
  .lesson-overlay button {
    position: absolute;
    top: 10px;
    right: 10px;
    background-color: #f44336;
    border: none;
    color: white;
    padding: 10px 20px;
    cursor: pointer;
    transition: background var(--transition-duration) ease;
  }
  .lesson-overlay button:hover {
    background-color: #c62828;
  }
  .lesson-overlay-data {
    background-color: white;
    width: fit-content;
    max-width: 80vw;
    margin: 5rem auto;
    padding: 2rem;
    max-height: 80vh;
    overflow-y: auto;
    border-radius: 1rem;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1), 0 1px 3px rgba(0, 0, 0, 0.06);
    color: #000;
    line-height: 1.6;
    column-count: 2;
    column-gap: 20px;
  }
  @media (max-width: 768px) {
    .lesson-overlay-data {
      column-count: 1;
      padding: 1.5rem;
      margin: 2rem;
    }
  }
  
  /* ==========================================================================
     WORDLIST & ARTICLE COMPONENTS
     ========================================================================== */
  .wordlist-container {
    padding: 2rem;
    background-color: #f9f9f9;
    border-radius: 1rem;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    font-family: "Open Sans", sans-serif;
    color: #333;
  }
  .wordlist-container h2 {
    text-align: center;
    margin-bottom: 2rem;
    font-size: 2rem;
    color: #444;
    text-transform: uppercase;
  }
  .wordlist {
    list-style-type: none;
    padding: 0;
    margin: 0;
  }
  .wordlist-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border: 1px solid #ddd;
    border-radius: 8px;
    background: #fff;
    transition: box-shadow 0.3s ease;
    padding: 0.5rem;
    margin-bottom: 0.5rem;
  }
  .wordlist-item:hover {
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);
  }
  .translation {
    font-weight: bold;
    font-size: 1rem;
  }
  .no-translation {
    font-style: italic;
    color: #999;
  }
  .example {
    font-size: 1rem;
    font-style: italic;
    color: #777;
    margin-top: 0.5rem;
    padding-left: 1rem;
    border-left: 3px solid #ddd;
  }
  .wordlist-container ul li:last-child {
    margin-bottom: 0;
  }
  
  /* ==========================================================================
     ARTICLE COMPONENT
     ========================================================================== */
  .article {

  }
  .article img {
    width: 100%;
    height: auto;
    margin-bottom: 1.5rem;
    border-radius: 0.5rem;
  }
  .article h1,
  .article h2,
  .article h3,
  .article h4,
  .article h5,
  .article h6 {
    font-family: 'Playfair Display', serif;
    color: #222;
    margin: 1.5rem 0 1rem;
  }
  .article p {
    line-height: 1.8;
    margin-bottom: 1.5rem;
    font-size: 1.125rem;
  }
  .article blockquote {
    font-style: italic;
    color: #666;
    border-left: 4px solid #ccc;
    padding-left: 1rem;
    margin: 0 0 1rem;
  }
  .article ul,
  .article ol {
    margin-left: 2rem;
    margin-bottom: 1.5rem;
  }
  .article a {
    color: #0072ff;
    text-decoration: none;
    border-bottom: 1px solid #0072ff;
    transition: color 0.3s ease, border-bottom-color 0.3s ease;
  }
  .article a:hover {
    color: #005bb5;
    border-bottom-color: #005bb5;
  }
  
  /* Responsive Article */
  @media (max-width: 768px) {
    .article {
      padding: 1.5rem;
    }
    .article p {
      font-size: 1rem;
    }
  }
  
  /* ==========================================================================
     TASK STYLES
     ========================================================================== */
  .task {
    background-color: var(--task-bg);
    padding: 1.5rem;
    margin: 2rem 0;
    border-radius: 0.5rem;
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.1);
    position: relative;
    overflow: hidden;
  }
  .task::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 0.25rem;
    height: 100%;
    background-color: var(--task-stripe);
  }
  .task h3 {
    margin: 0;
    color: var(--base-text);
    display: flex;
    align-items: center;
  }
  .task h3::before {
    content: '📝';
    display: inline-block;
    margin-right: 0.75rem;
    font-size: 1.5rem;
  }
  
  /* ==========================================================================
     GRAMMAR BOX & LP (Lesson Panel) STYLES
     ========================================================================== */
  .grammar-box {
    background: linear-gradient(135deg, var(--grammar-box-bg), var(--grammar-box-alt));
    border-radius: 15px;
    padding: 20px;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
    transition: all var(--transition-duration) ease;
    color: #181a20;
    border: 2px solid #FF76CE;
    margin: 20px auto;
    max-width: 600px;
  }
  /* Sophisticated Black and Muted Yellow Themed Container with Lovely Backgrounds */
.boxify {
  /* Custom Property Definitions */
  --font-color: #1a1a1a;         /* Deep, sophisticated black */
  --font-color-sub: #555555;     /* Subdued secondary text color */
  --bg-color: #fff9e6;           /* Soft, warm off-white with a yellow tint */
  --main-color: #1a1a1a;         /* Primary text and border color */
  --accent-color: #f2c94c;       /* Muted, elegant yellow accent */

  /* Layout & Spacing */
  height: 100%;
  padding: 20px;
  margin: 1rem;
  gap: 10px;

  /* Background, Border & Shadow */
  background: linear-gradient(135deg, var(--bg-color), #ffffff);
  border: 2px solid var(--main-color);
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(26, 26, 26, 0.1);

  /* Typography */
  color: var(--font-color);
  font-family: 'Helvetica Neue', Arial, sans-serif;
  font-size: 1rem;
  line-height: 1.6;
}

/* Sophisticated Letter/Quote Block with a Lovely, Light Background */
.letter {
  /* Typography & Spacing */
  font-family: 'Georgia', serif;
  font-size: 1rem;
  line-height: 1.8;
  color: var(--font-color);
  padding: 15px 20px;
  margin: 1rem 0;

  /* Background & Accent Border */
  background-color: #fffde7;    /* A very pale, warm yellow for an inviting look */
  border-left: 5px solid var(--accent-color);
  border-radius: 4px;

  /* Subtle Shadow for Depth */
  box-shadow: 2px 2px 6px rgba(26, 26, 26, 0.05);
}

  .lp-top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0.5rem 1.5rem;
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);
    background: var(--lp-bg);
    color: #181a20;
    border: 1px solid var(--lp-border);
  }
  .uppercase {
    text-transform: uppercase;
  }
  .lp-tags {
    display: contents;
  }
  .lp-tag {
    background: rgba(24, 26, 32, 0.9);
    color: #fcd535;
    border-radius: 12px;
    padding: 0.4rem 0.8rem;
    font-size: 0.9rem;
    font-weight: bold;
    text-align: center;
    white-space: nowrap;
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.2);
    border: 1px solid #d1a800;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 2px;
    transition: transform 0.2s, box-shadow 0.2s;
    min-width: 60px;
  }
  .lp-tag:hover {
    transform: scale(1.05);
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
  }
  .lp-tag-title {
    font-size: 0.8rem;
    color: #fff;
    text-transform: uppercase;
  }
  .lp-tag-content {
    font-size: 1rem;
    color: #fcd535;
  }
  .wide {
    flex: 1;
    text-align: center;
    font-size: 1.5rem;
    font-weight: bold;
    color: #181a20;
    text-transform: uppercase;
    letter-spacing: 1.5px;
  }
  .yel-red {
    background: linear-gradient(135deg, #f3ec78, #af4261);
  }
  .turk-red {
    background: linear-gradient(135deg, #30D5C8, #a30f39);
  }
  
  /* ==========================================================================
     HELPER BUTTON & TOOLTIP (Helperz)
     ========================================================================== */
  .helperz {
    position: fixed;
    bottom: 16px;
    right: 16px;
    z-index: 1000;
    background: none;
    box-shadow: none;
  }
  .helperz .main-button {
    width: 100px;
    height: 100px;
    background: var(--helperz-bg);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
    cursor: pointer;
    transition: transform var(--transition-duration) ease, background var(--transition-duration) ease;
  }
  .helperz .main-button:hover {
    transform: scale(1.1);
    background: aqua;
  }
  .helperz .main-button.active {
    transform: scale(1.1);
    background: red;
  }
  .helperz .fixed-button {
    display: flex;
    flex-direction: column;
    gap: 12px;
    margin-top: 12px;
    align-items: flex-end;
  }
  .helperz .fixed {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 8px;
    background: var(--helperz-bg);
    color: var(--helperz-text);
    padding: 8px 16px;
    border-radius: 20px;
    cursor: pointer;
    transition: all var(--transition-duration) ease;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2);
  }
  .helperz .fixed:hover {
    background: var(--hover-bg, #ffffff26);
    transform: translateY(-2px);
  }
  .helperz .tipper {
    font-size: 14px;
    font-weight: bold;
  }
  .helperzImg {
    max-width: 100%;
    max-height: 100%;
    object-fit: cover;
    border-radius: 50%;
    box-shadow: none !important;
    padding: 0 !important;
    display: block;
  }
  .helperz .hover-text {
    position: absolute;
    left: -190%;
    top: 50%;
    transform: translateY(-250%);
    background: var(--secondary-bg, #fcd535);
    color: var(--secondary-text, #181a20);
    padding: 4px 8px;
    font-size: 14px;
    border-radius: 4px;
    opacity: 0;
    visibility: hidden;
    transition: opacity var(--transition-duration) ease, visibility var(--transition-duration) ease;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2);
    max-width: 300px;
    white-space: normal;
  }
  .helperz .main-button:hover .hover-text {
    opacity: 1;
    visibility: visible;
  }
  @media (max-width: 400px) {
    .helperz .hover-text {
      left: -120%;
      max-width: 200px;
    }
  }
  
  /* ==========================================================================
     TEXTAREA STYLES
     ========================================================================== */
  textarea {
    width: 100%;
    max-width: 600px;
    padding: 1rem;
    font-size: 1rem;
    color: #333;
    background: linear-gradient(135deg, #f0f0f0, #ffffff);
    border: 2px solid transparent;
    border-radius: var(--border-radius);
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    outline: none;
    transition: all var(--transition-duration) ease-in-out;
    resize: none;
    overflow: hidden;
    height: auto;
  }
  textarea:focus {
    background: #ffffff;
    border-color: #FF76CE;
    box-shadow: 0 4px 12px rgba(255, 118, 206, 0.4);
    transform: translateY(-2px);
  }
  textarea::placeholder {
    color: #aaa;
    font-style: italic;
    transition: color var(--transition-duration) ease;
  }
  textarea:focus::placeholder {
    color: #FF76CE;
  }
  textarea:hover {
    border-color: #FDFFC2;
    box-shadow: 0 4px 10px rgba(253, 255, 194, 0.3);
  }
  textarea:disabled {
    background: #eaeaea;
    color: #999;
    border-color: #ccc;
    cursor: not-allowed;
  }
  .part > textarea {
    min-height: 400px;
  }
  .tasklist{
    flex: 1
  }
  .mane.reading-grid {
    display: grid;
    grid-template-columns: 1fr 1fr; /* left frame takes 1/3, right takes 2/3; adjust as needed */
    grid-gap: 1rem; /* spacing between the frames */
  }
  
  .left-frame,
.right-frame {
  /* The max height is calculated to subtract header/footer (or any other fixed elements) */
  max-height: calc(100vh - 57.6px - 70px);
  overflow-y: auto;
  /* For a cleaner look, add some padding and border */
  padding: 1rem;
  background: #fff;
  border: 1px solid #ddd;
  border-radius: 4px;
}
/* For WebKit-based browsers (Chrome, Safari, Edge) */
.left-frame::-webkit-scrollbar,
.right-frame::-webkit-scrollbar {
  width: 12px; /* Adjust width as desired */
}

.left-frame::-webkit-scrollbar-track,
.right-frame::-webkit-scrollbar-track {
  background: #000; /* Black background for the track */
  border-radius: 10px; /* Rounded track edges */
}

.left-frame::-webkit-scrollbar-thumb,
.right-frame::-webkit-scrollbar-thumb {
  background-color: #FFD700; /* Bright yellow thumb */
  border: 2px solid #000; /* Black border to accent the thumb */
  border-radius: 10px; /* Rounded thumb edges */
  transition: background-color 0.3s ease;
}

.left-frame::-webkit-scrollbar-thumb:hover,
.right-frame::-webkit-scrollbar-thumb:hover {
  background-color: #FFC107; /* A slightly different yellow on hover */
}

/* For Firefox */
.left-frame,
.right-frame {
  scrollbar-width: thin;             /* Make the scrollbar a bit thinner */
