/* src/components/Vault/IrregularVerbsTrainer.css */

.trainer-container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding: 2rem;
    max-width: 1200px;
    margin: 0 auto;
    background-color: #f9f9f9;
  }
  .solid{
    grid-column: 1/3;
  }
  .trainer-container h1 {
    text-align: center;
    font-family: "Playfair Display", serif;
  }
  
  .trainer-container h3 {
    text-align: center;
    color: crimson;
    font-family: "Playfair Display", serif;
  }
  
  .block {
    text-align: justify;
    width: 60%;
    margin: 1.5rem auto;
    background-color: #FFBB64;
    border-radius: 15px;
    padding: 1rem;
    font-family: 'Merriweather', serif;
  }
  
  .left {
    background-color: #f0f0f0;
    padding: 20px;
    filter: blur(5px);
    cursor: pointer;
    transition: filter 0.3s ease;
    margin-bottom: 1.5rem;
  }
  
  .left.unblur {
    filter: none;
  }
  
  .right {
    padding: 20px;
    display: grid;
    grid-gap: 10px;
    overflow-x: auto;
  }
  
  .verb-table, .check-table {
    margin: auto;
    font-family: 'Merriweather', serif;
    font-size: 1.1rem;
    border-collapse: collapse;
    border-radius: 15px;
    width: 100%;
  }
  
  .verb-table th, .verb-table td,
  .check-table th, .check-table td {
    padding: 0.2rem;
    border: 1px solid black;
    text-align: center;
  }
  
  .verb-table thead, .check-table thead {
    background-color: turquoise;
  }
  
  .verb-table tbody tr:nth-child(even) {
    background-color: #FDFFAB;
  }
  
  .verb-table tbody tr:nth-child(odd) {
    background-color: #FF8080;
  }
  
  .check-table tbody tr td:first-child {
    background-color: #7BD3EA;
    font-weight: 800;
  }
  
  .trainer {
    border: none;
    width: 100%;
    height: 100%;
    padding: 0.3rem;
    box-sizing: border-box;
  }
  
  .trainer:focus {
    outline: 2px solid #4CAF50;
  }
  
  .correct-indicator {
    margin-left: 5px;
    color: green;
    font-weight: bold;
  }
  
  @media (max-width: 700px) {
    .trainer-container {
      padding: 1rem;
    }
  
    .block {
      width: 90%;
    }
  
    .buttons-container {
      flex-direction: column;
    }
  
    .verb-table, .check-table {
      font-size: 1rem;
    }
  }
  