/* Backdrop */
.course-overlay-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  animation: fadeIn 0.3s ease-in-out;
}

/* Overlay Content */
.course-overlay-content {
  background: linear-gradient(135deg, #fcd535, #000);
  color: white;
  width: 90%;
  max-width: 600px;
  max-height: 90vh; /* Prevents exceeding screen height */
  border-radius: 12px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
  padding: 2rem;
  position: relative;
  overflow-y: auto; /* Enables scrolling if content exceeds height */
  animation: slideUp 0.5s ease;
  scrollbar-width: thin; /* For custom scrollbar styling */
  scrollbar-color: #fcd535 #000;
}

/* Close Button */
.course-close-button {
  position: absolute;
  top: 1rem;
  right: 1rem;
  background: none;
  color: white;
  border: none;
  font-size: 1.5rem;
  cursor: pointer;
  transition: transform 0.3s ease;
}

.course-close-button:hover {
  transform: rotate(90deg);
}

/* Header */
.overlay-header h2 {
  font-size: 1.8rem;
  margin-bottom: 0.5rem;
  text-transform: uppercase;
  color: #fcd535;
}

.overlay-header p {
  font-size: 1.1rem;
  line-height: 1.5;
}

/* Details Section */
.details-section {
  margin-top: 1.5rem;
}

.details-section h3 {
  font-size: 1.4rem;
  color: #fcd535;
  margin-bottom: 0.5rem;
}

.details-section p,
.details-section ul {
  font-size: 1rem;
  line-height: 1.5;
}

.details-section ul {
  list-style: disc;
  margin-left: 1.5rem;
}

/* CTA Button */
.overlay-cta {
  text-align: center;
  margin-top: 2rem;
}

.enroll-button {
  background: #fcd535;
  color: #000;
  font-size: 1.2rem;
  padding: 0.8rem 1.5rem;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background 0.3s ease, transform 0.2s ease;
}

.enroll-button:hover {
  background: #fff200;
  transform: scale(1.05);
}

/* Media Queries for Smaller Screens */
@media (max-width: 768px) {
  .course-overlay-content {
    padding: 1.5rem; /* Reduce padding */
    width: 95%; /* Wider on smaller screens */
  }

  .overlay-header h2 {
    font-size: 1.5rem; /* Smaller font size */
  }

  .details-section h3 {
    font-size: 1.2rem; /* Adjust headers */
  }

  .enroll-button {
    font-size: 1rem; /* Smaller button */
    padding: 0.7rem 1.2rem; /* Adjust padding */
  }
}

/* Scrollbar Styling */
.course-overlay-content::-webkit-scrollbar {
  width: 8px;
}

.course-overlay-content::-webkit-scrollbar-track {
  background: #000;
}

.course-overlay-content::-webkit-scrollbar-thumb {
  background-color: #fcd535;
  border-radius: 8px;
}
