/* Admin container setup */
.admin-container {
  display: flex;
  height: 100vh;
  background-color: #f5f5f5;
}

/* Sidebar */
.sidebar-adm {
  width: 250px;
  background-color: #333;
  color: white;
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  box-shadow: 2px 0 5px rgba(0, 0, 0, 0.1);
}

.sidebar-adm h2 {
  color: #fff;
  margin-bottom: 20px;
  font-size: 1.5rem;
}

.sidebar-adm ul {
  list-style: none;
  padding: 0;
  width: 100%;
}

.sidebar-adm ul li {
  margin: 15px 0;
}

.sidebar-adm ul li.active {
  background-color: #ff76ce;
  padding: 10px;
  border-radius: 5px;
}

.sidebar-adm ul li a {
  color: white;
  text-decoration: none;
  font-size: 1rem;
  display: block;
}

.sidebar-adm button {
  background-color: #fcd535;
  border: none;
  padding: 10px 20px;
  color: #181a20;
  cursor: pointer;
  border-radius: 5px;
  font-size: 16px;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

.sidebar-adm button:disabled {
  background-color: #ddd;
  cursor: not-allowed;
}

.sidebar-adm button:hover {
  background-color: #ff76ce;
  transform: scale(1.05);
}

/* Main content */
.main-content {
  flex: 1;
  padding: 20px;
  background-color: white;
  border-left: 1px solid #ddd;
}

/* User stats section */
.user-stats {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-bottom: 30px;
  gap: 20px;
}

.stats-card {
  background-color: #94ffd8;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  flex: 1 1 200px;
  min-width: 200px;
}

.stats-card p {
  margin: 10px 0;
  font-size: 1rem;
  color: #333;
}

/* User table section */
.user-table {
  margin-top: 30px;
  overflow-x: auto;
}

.user-table table {
  width: 100%;
  border-collapse: collapse;
}

.user-table th,
.user-table td {
  padding: 12px;
  text-align: left;
  border-bottom: 1px solid #ddd;
  font-size: 0.9rem;
}

.user-table th {
  background-color: #fcd535;
  color: #181a20;
  text-transform: uppercase;
}

.user-table td {
  background-color: #f9f9f9;
}

.user-table td button {
  background-color: #ff76ce;
  color: white;
  border: none;
  padding: 5px 10px;
  cursor: pointer;
  border-radius: 5px;
  margin-right: 5px;
  font-size: 0.85rem;
}

.user-table td button:hover {
  background-color: #fdffc2;
  color: #333;
}

/* Responsive Design */
@media (max-width: 768px) {
  .admin-container {
    flex-direction: column;
  }

  .sidebar-adm {
    width: 100%;
    height: auto;
    box-shadow: none;
  }

  .main-content {
    margin-top: 20px;
    padding: 15px;
  }

  .user-stats {
    justify-content: center;
  }

  .stats-card {
    min-width: 100%;
  }

  .user-table table {
    font-size: 0.8rem;
  }

  .user-table th, 
  .user-table td {
    padding: 10px;
  }
}
