.profile-page {
  display: flex;
  min-height: 100vh;
  background: linear-gradient(180deg, #121212 0%, #1c1c1c 100%);
  color: white;
  margin: 0;
  padding: 0;
}

/* Sidebar */
.floating-button{
  display: none;
}
.sidebar {
  width: 250px;
  background: rgba(30, 30, 30, 0.8); /* Glassmorphism */
  backdrop-filter: blur(10px);
  border-right: 1px solid rgba(255, 255, 255, 0.1);
  padding: 20px 10px;
}

.sidebar-set {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.sidebar-items {
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 10px;
  border-radius: 10px;
  transition: background 0.3s ease-in-out;
  text-decoration: none;
  color: white;
  font-weight: bold;
}

.sidebar-items:hover {
  background: rgba(252, 213, 53, 0.2);
  color: #fcd535;
}

.sidebar-items .material-symbols-outlined {
  font-size: 1.5rem;
}

/* Logout Button */
.logout-button {
  background: none;
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 10px;
  padding: 10px;
  color: white;
  font-size: 1rem;
  font-weight: bold;
  cursor: pointer;
  transition: background 0.3s ease-in-out;
}

.logout-button:hover {
  background: rgba(252, 213, 53, 0.2);
  color: #fcd535;
}

/* Main Section */
.main-section {
  flex-grow: 1;
  padding: 20px;
}
.profile-info {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 20px;
  padding: 10px;
}
.profile-image-wrapper{
  display: flex;
  flex-direction: column;
  align-items: center;
}

.profile-header {
  display: flex;
  gap: 20px;
  padding: 20px;
  background: rgba(40, 40, 40, 0.9);
  border-radius: 15px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
  margin-bottom: 20px;
}

.profile-image-container {
  flex-shrink: 0;
}

.profile-image {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  border: 3px solid #fcd535;
}

.profile-name-container {
  display: flex;
  flex-direction: column;
}

/* Profile Name Styling */
.profile-name {
  font-size: 1.8rem;
  font-weight: bold;
  background: linear-gradient(90deg, #fcd535, #ff7f50, #ff4500); /* Gradient Effect */
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent; /* Ensures only the gradient is visible */
  margin: 10px 0;
  display: inline-block;
}

.profile-details {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  gap: 15px;
}

.detail-item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 10px 15px;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 10px;
  text-align: center;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  transition: transform 0.2s ease, box-shadow 0.2s ease;
}
.detail-item:hover {
  transform: scale(1.05);
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
}
.detail-label, .profile-label {
  font-size: 1rem;
  color: #fcd535;
  font-weight: bold;
  margin-bottom: 5px;
}
.detail-value{
  color: white;
  text-wrap: wrap;
}
/* Balance Section */
/* Balance Section */
.balance-section {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: space-evenly; /* Distributes content evenly */
  align-items: flex-start;
  padding: 20px;
  background: rgba(40, 40, 40, 0.9);
  border-radius: 15px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
}

/* Balance Box */
.balance-box {
  flex: 1; /* Flex-grow to adjust width */
  min-width: 250px; /* Prevents boxes from getting too small */
  max-width: 400px; /* Sets a maximum size for better usability */
  padding: 20px;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 10px;
  text-align: center;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  transition: transform 0.2s ease, box-shadow 0.2s ease;
}

.balance-box:hover {
  transform: scale(1.05);
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
}

/* Balance Content Title */
.balance-title {
  font-size: 1.2rem;
  color: white;
  font-weight: bold;
  margin-bottom: 10px;
}

/* Recharge Buttons */
.recharge-btn {
  display: inline-block;
  margin-top: 15px;
  padding: 10px 20px;
  background: #fcd535;
  color: black;
  font-weight: bold;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: transform 0.2s ease;
}

/* Nextobox Container */
.nextobox {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: center; /* Centers items */
  align-items: stretch; /* Ensures items are the same height */
  padding: 10px;
  background: rgba(40, 40, 40, 0.9);
  border-radius: 15px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
}

/* Next Lesson and Workbook Boxes */
.next-lesson-box {
  flex: 1; /* Ensures the boxes share available space */
  min-width: 250px; /* Prevents boxes from shrinking too much */
  max-width: 400px; /* Keeps a consistent maximum size */
  background: rgba(255, 255, 255, 0.05);
  border-radius: 10px;
  padding: 15px;
  text-align: center;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  transition: transform 0.2s ease, box-shadow 0.2s ease;
}

.next-lesson-box:hover {
  transform: scale(1.05);
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
}

/* Lesson/Workbook Text */
.next-lesson-text {
  font-size: 1.2rem;
  color: #fcd535;
  font-weight: bold;
  margin-bottom: 10px;
}

.lesson-details,
.workbook-details {
  margin: 10px 0;
  font-size: 0.9rem;
  color: white;
  line-height: 1.5;
}

/* Responsive Images for Next Lesson/Workbook */
.logoNext {
  max-width: 100%; /* Ensures image scales properly */
  height: auto; /* Maintains aspect ratio */
  margin-top: 15px;
  border-radius: 5px; /* Optional rounded edges for aesthetic */
}

/* Step Items */
.step-item {
  display: flex;
  flex: 1 1 calc(33.333% - 20px);
