/* Universal style for incomplete elements */
.incomplete {
  background-color: #f5f5f5; /* Light grey background */
  color: #d6d6d6; /* Slightly dimmed text color */
  border: 2px dashed #ccc; /* Dashed border to indicate incompleteness */
  cursor: not-allowed; /* Shows a disabled cursor */
  position: relative; /* For positioning pseudo-elements */
  padding: 10px;
  border-radius: 5px;
  transition: all 0.3s ease; /* Smooth transition for changes */
}

/* Add an icon or visual indicator */
.incomplete::before {
  content: "🔒"; /* Lock emoji to signify incomplete */
  font-size: 18px;
  position: absolute;
  top: 10px;
  right: 10px;
}

/* Hover effect */
.incomplete:hover {
  background-color: #f0f0f0; /* Slightly lighter grey when hovering */
  border-color: #aaa; /* Lighter dashed border when hovering */
  color: #bbb; /* Slightly brighter text */
}
