/* Progress bar container */
.progress-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 40px;
  position: relative;
  width: 100%;
}

.progress-bar {
  position: absolute;
  top: 10px;
  left: 0;
  width: 100%;
  height: 6px;
  background-color: #2c2c2c;
  border-radius: 10px;
}

.progress-bar-filled {
  height: 100%;
  background-color: #fcd535;
  border-radius: 10px;
}

.progress-step {
  position: relative;
  flex: 1;
  text-align: center;
  font-size: 1.2rem;
  font-weight: bold;
  color: #fcd535;
  z-index: 1;
  transform: translateY(100%);
}

.progress-step.completed {
  color: green; /* Green for completed steps */
}

.progress-step.in-progress {
  color: #FF76CE; /* Pink for the current step */
}

.progress-step:before {
  content: '';
  position: absolute;
  top: 0;
  left: 50%;
  width: 18px;
  height: 18px;
  background-color: #fff;
  border-radius: 50%;
  transform: translate(-50%, -100%);
  z-index: 2;
}

