.logout-button {
  margin-top: auto;
  padding: 10px;
  border: none;
  background: rgba(255, 0, 0, 0.8);
  color: white;
  cursor: pointer;
  text-align: center;
  border-radius: 10px;
}

.logout-button:hover {
  background: red;
}

/* Background Overlay */
.sidebar-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  z-index: 999;
}
/* General styles for links */
a {
  text-decoration: none;
  color: inherit; /* Inherit the color from the parent element */
}

/* Prevent style changes on hover */
a:hover {
  text-decoration: none;
  color: inherit;
}