/* VaultWordlistOverview.css */

/* Container for the wordlist overview using a grid layout with flex behavior */
.wordlist {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 1.5rem;
    padding: 1rem;
    list-style: none;
    background: linear-gradient(135deg, #f6f8fa, #e9eff5);
  }
  
  /* Each word card styled as a gamified box */
  .wordlist-item {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    border: 1px solid transparent;
    border-radius: 12px;
    background: linear-gradient(135deg, #ffffff, #f1f9ff);
    padding: 1.25rem;
    box-shadow: 0 6px 10px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease, box-shadow 0.3s ease, border 0.3s ease;
    position: relative;
    overflow: hidden;
  }
  
  /* Hover effect: subtle lift, scale and border highlight */
  .wordlist-item:hover {
    transform: translateY(-8px) scale(1.02);
    box-shadow: 0 10px 15px rgba(0, 0, 0, 0.15);
    border: 1px solid #f6a724;
  }
  
  /* Word header with a vibrant gradient for instant visual appeal */
  .word-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: linear-gradient(90deg, #f6a724, #ff9f43);
    padding: 0.75rem 1rem;
    border-radius: 8px 8px 0 0;
    color: #fff;
    width: 100%;
  }
  
  .word {
    font-size: 1.3rem;
    font-weight: bold;
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.2);
  }
  
  .part-of-speech {
    font-size: 0.9rem;
    color: #fce2e2;
    font-style: italic;
  }
  
  /* Word details area with a soft background and inset shadow for depth */
  .word-details {
    margin-top: 1rem;
    padding: 0.75rem;
    background: #fff;
    border-radius: 0 0 8px 8px;
    box-shadow: inset 0 0 10px rgba(0, 0, 0, 0.05);
  }
  
  /* Translation displayed in an elegant, blue italic style */
  .translation {
    display: block;
    margin-bottom: 0.5rem;
    font-style: italic;
    color: #2f80ed;
  }
  
  /* Word info (e.g., definition or example) with a dashed border for a playful touch */
  .word-info {
    font-size: 0.95rem;
    margin: 0.5rem 0;
    padding-bottom: 0.5rem;
    border-bottom: 1px dashed #87ceeb;
  }
  .word-info:last-child {
    border-bottom: none;
  }
  
  /* Responsive adjustments for smaller screens */
  @media (max-width: 600px) {
    .wordlist-item {
      padding: 0.8rem;
    }
    .word {
      font-size: 1.1rem;
    }
    .translation,
    .word-info {
      font-size: 0.9rem;
    }
  }
  
  /* Additional classes (if used elsewhere) */
  .wb-testing {
    flex-direction: row;
  }
  
  .tasker-top {
    color: crimson;
  }
  /* --------------------------
   Pagination Controls Styles
--------------------------- */
.pagination-controls {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1rem;
    margin: 2rem 0;
  }
  
  .pagination-controls button {
    background: linear-gradient(135deg, #56CCF2, #2F80ED);
    color: #fff;
    border: none;
    padding: 0.75rem 1.2rem;
    border-radius: 50px;
    cursor: pointer;
    font-size: 1rem;
    font-weight: bold;
    transition: background 0.3s ease, transform 0.2s ease;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.15);
  }
  
  .pagination-controls button:hover {
    transform: scale(1.05);
    background: linear-gradient(135deg, #2F80ED, #56CCF2);
  }
  
  .pagination-controls span {
    font-size: 1.1rem;
    font-weight: bold;
    color: #333;
  }
  
  
  /* --------------------------
     Action Buttons Styles
  --------------------------- */
  .action-buttons {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1.5rem;
    margin-top: 2rem;
  }
  
  .action-buttons button {
    background: linear-gradient(135deg, #FF9F43, #F6A724);
    color: #fff;
    border: none;
    padding: 0.8rem 1.5rem;
    border-radius: 50px;
    cursor: pointer;
    font-size: 1rem;
    font-weight: bold;
    transition: background 0.3s ease, transform 0.2s ease;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.15);
  }
  
  .action-buttons button:hover {
    transform: scale(1.05);
    background: linear-gradient(135deg, #F6A724, #FF9F43);
  }
  .headering-wl{
    display: flex;
    flex-direction: row;
    gap: 2rem;
  }