:root {
  font-size: 16px; /* Base font size */
  --primary-gradient: linear-gradient(135deg, #ff9a9e, #fad0c4);
  --secondary-gradient: linear-gradient(135deg, #a18cd1, #fbc2eb);
  --battery-full: #00ff88;
  --battery-low: #ff4d4d;
}

/* Container for warming component */
.warming {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #ff9a9e, #fad0c4, #a18cd1, #fbc2eb);
  background-size: 200% 200%;
  padding: 2rem 1.5rem;
  border-radius: 1rem;
  box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.15);
  animation: fadeIn 1s ease-out, gradientShift 5s infinite alternate;
  color: #222;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  position: relative;
  overflow: hidden;
}

.warming:hover {
  transform: scale(1.02);
  box-shadow: 0 1.5rem 3rem rgba(0, 0, 0, 0.2);
}

/* Title with sparkle effect */
.warming-title {
  font-size: 2rem;
  font-weight: 600;
  margin-bottom: 1.5rem;
  color: #fff;
  text-shadow: 0 0.2rem 0.5rem rgba(0, 0, 0, 0.2);
  position: relative;
}

.warming-title::after {
  content: '';
  position: absolute;
  top: -0.5rem;
  left: -0.5rem;
  width: 1rem;
  height: 1rem;
  background: rgba(255, 255, 255, 0.8);
  border-radius: 50%;
  animation: sparkle 2s infinite;
}

/* Warming content layout */
.warming-content {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

/* Battery container */
.battery-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-right: 2rem;
}

/* Battery body with glowing effect */
.battery {
  position: relative;
  width: 4rem;
  height: 8rem;
}

.battery-body {
  position: relative;
  width: 100%;
  height: 100%;
  border: 0.2rem solid #fff;
  border-radius: 0.5rem;
  overflow: hidden;
  background: var(--secondary-gradient);
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.1);
  animation: glow 2s infinite alternate;
}

/* Battery level with pulse animation */
.battery-level {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 0%;
  background: var(--battery-low);
  transition: height 0.5s linear, background 0.5s ease-in-out;
  animation: pulse 1.5s infinite;
}

.battery-level.full {
  background: var(--battery-full);
  animation: pulse 1.5s infinite;
}

/* Battery cap */
.battery-cap {
  position: absolute;
  top: -0.7rem;
  left: 1rem;
  width: 2rem;
  height: 0.7rem;
  background: #fff;
  border-radius: 0.2rem 0.2rem 0 0;
}

/* Battery label */
.battery-label {
  margin-top: 0.75rem;
  font-size: 1.1rem;
  font-weight: 500;
  color: #fff;
}

/* Progress bar for charging */
.progress-bar {
  width: 100%;
  height: 0.5rem;
  background: rgba(255, 255, 255, 0.3);
  border-radius: 0.25rem;
  margin-top: 1rem;
  overflow: hidden;
  position: relative;
}

.progress {
  width: 0%;
  height: 100%;
  background: var(--battery-full);
  border-radius: 0.25rem;
  transition: width 0.5s ease-in-out;
}

.progress-label {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 0.75rem;
  color: #fff;
  text-shadow: 0 0.1rem 0.3rem rgba(0, 0, 0, 0.2);
}

/* Parsed content with fade-in animation */
.parsed-content {
  max-width: 40rem;
  text-shadow: 0 0.1rem 0.3rem rgba(0, 0, 0, 0.2);
  animation: fadeInUp 1s ease-out 0.5s forwards;
  opacity: 0;
}

/* Confetti effect */
.confetti-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  pointer-events: none;
}

.confetti {
  position: absolute;
  width: 0.5rem;
  height: 0.5rem;
  background: #ff0;
  border-radius: 50%;
  animation: confetti 2s ease-out;
}

/* Keyframes for animations */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: scale(0.95);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(1.5rem);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes glow {
  0% {
    box-shadow: 0 0 0.5rem rgba(255, 255, 255, 0.5);
  }
  100% {
    box-shadow: 0 0 1.5rem rgba(255, 255, 255, 0.8);
  }
}

@keyframes pulse {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.7;
  }
  100% {
    opacity: 1;
  }
}

@keyframes sparkle {
  0% {
    transform: scale(0);
    opacity: 0;
  }
  50% {
    transform: scale(1);
    opacity: 1;
  }
  100% {
    transform: scale(0);
    opacity: 0;
  }
}

@keyframes gradientShift {
  0% {
    background-position: 0% 50%;
  }
  100% {
    background-position: 100% 50%;
  }
}

@keyframes confetti {
  0% {
    transform: translateY(0) rotate(0);
    opacity: 1;
  }
  100% {
    transform: translateY(100vh) rotate(720deg);
    opacity: 0;
  }
}

/* Media queries for responsiveness */
@media (max-width: 768px) {
  :root {
    font-size: 14px;
  }

  .warming {
    padding: 1.5rem 1rem;
  }

  .warming-title {
    font-size: 1.75rem;
  }

  .warming-content {
    flex-direction: column;
  }

  .battery-container {
    margin-right: 0;
    margin-bottom: 1.5rem;
  }

  .battery {
    width: 3rem;
    height: 6rem;
  }

  .battery-cap {
    left: 0.75rem;
    width: 1.5rem;
    top: -0.5rem;
  }

  .battery-label {
    font-size: 0.95rem;
  }

  .parsed-content {
    font-size: 1.1rem;
  }
}
