/* src/components/CoursesPage/CourseCard.css */

.course-card {
    position: relative;
    cursor: pointer;
    overflow: hidden;
    border-radius: 8px;
  }
  
  .course-card-image {
    width: 100%;
    height: 100%;
    object-fit: cover;
    display: block;
    transition: transform 0.3s ease;
  }
  
  .course-card:hover .course-card-image {
    transform: scale(1.05);
  }
  
  .course-card-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    /* Darken overlay effect */
    background: linear-gradient(to bottom, rgba(0,0,0,0.2), rgba(0,0,0,0.6));
    color: #fff;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    padding: 1rem;
    opacity: 0;
    transition: opacity 0.3s ease;
  }
  
  .course-card:hover .course-card-overlay {
    opacity: 1;
  }
  
  .course-card-overlay h3 {
    margin: 0;
    font-size: 1.2rem;
    font-weight: bold;
    color: #FFD700; /* Yellow */
  }
  
  .course-card-overlay p {
    margin: 0.25rem 0 0;
    font-size: 0.9rem;
  }
  