.error-box {
    position: relative;
    width: 100%;
    height: 91.7vh;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    text-align: center;
  }
  
  .error-bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: 1;
    filter: brightness(0.6); /* Darkens the image for better text visibility */
  }
  
  .error-text {
    position: relative;
    z-index: 2;
    font-size: 2em;
    bottom: -150px;
    padding: 20px;
    background: rgba(0, 0, 0, 0.5); /* Adds a semi-transparent background to the text */
    border-radius: 10px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.5); /* Adds depth to the text container */
  }
  