.courses-page {
  color: #fff; /* White text by default */
  min-height: 100vh; /* Ensures it takes up the full viewport height */
  display: flex;
  flex-direction: column; /* Stack hero, grid, and future-courses vertically */
  justify-content: space-between; /* Evenly distribute sections */
  padding: 2rem;
  box-sizing: border-box; /* Includes padding in height calculation */
  overflow-y: auto; /* Enables scrolling if content exceeds viewport */

  background-color: #000000;
opacity: 0.9;
background-image:  radial-gradient(#fcd535 0.5px, transparent 0.5px), radial-gradient(#fcd535 0.5px, #000000 0.5px);
background-size: 20px 20px;
background-position: 0 0,10px 10px;
}

.courses-hero {
  text-align: center;
}

.courses-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1.5rem;
  justify-content: center; /* Centers the entire grid horizontally */
  justify-items: center; /* Centers individual items within their cells */
  padding: 1rem;
  flex-grow: 1; /* Ensures the grid expands to fill available space */
  margin: 0 15rem;;
}

@media (min-width: 900px) {
  .courses-grid {
    grid-template-columns: repeat(3, minmax(250px, 1fr));
  }
}

/* Ensure the last row items are centered */
.courses-grid > :nth-last-child(-n+3) {
  grid-column: span 1; /* Ensures each item spans only one column */
  justify-self: center; /* Centers the item horizontally */
}

/* Future Courses Section */
.future-courses {
  text-align: center;
  margin-top: 2rem;
  padding: 2rem;
  background-color: #f7f7f7;
  border-radius: 8px;
  color: #000; /* Ensure good contrast */
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2); /* Add subtle depth */
}

/* Responsive Adjustments */
@media (max-width: 768px) {
  .courses-page {
    padding: 1rem; /* Reduce padding for smaller screens */
  }

  .courses-grid {
    gap: 1rem; /* Tighter spacing between grid items */
  }

  .future-courses {
    padding: 1rem; /* Compact padding */
  }
}
