.link {
  display: block;
  text-align: center;
  color: #777;
  text-decoration: none;
  padding: 10px;
}

.movies-container {
  padding: 20px;
}

.movie_card {
  position: relative;
  display: block;
  width: 100%;
  max-width: 700px;
  height: 250px;
  margin: 20px auto;
  overflow: hidden;
  border-radius: 12px;
  transition: all 0.3s ease;
  box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.2);
  background-color: #fff;
  &:hover {
    transform: scale(1.03);
    box-shadow: 0px 6px 20px rgba(0, 0, 0, 0.3);
  }
}

.info_section {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  height: 100%;
  padding: 20px;
  z-index: 2;
}

.movie_header {
  display: flex;
  align-items: center;
  gap: 15px;
}

.locandina {
  width: 80px;
  height: 120px;
  border-radius: 8px;
  box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.2);
}

h1 {
  font-size: 18px;
  font-weight: 700;
  margin: 0;
  color: #222;
}

h4 {
  font-size: 14px;
  font-weight: 400;
  color: #666;
  margin: 5px 0 0;
}

.minutes {
  display: inline-block;
  font-size: 12px;
  color: #888;
  padding: 4px 8px;
  border-radius: 5px;
  background-color: #f0f0f0;
  margin-top: 5px;
}

.movie_desc {
  margin-top: 10px;
  font-size: 14px;
  color: #555;
}

.blur_back{
  position: absolute;
  top: 0;
  width: 100%;
  left: 30%;
  z-index: 1;
  height: 100%; right: 0;
  background-size: cover;
  border-radius: 11px;
  mask-image: linear-gradient(to left, black 60%, transparent 100%);
  -webkit-mask-image: linear-gradient(to left, black 60%, transparent 100%);
}

@media screen and (max-width: 768px) {
  .movie_card {
    height: 200px;
  }
  .movie_header {
    flex-direction: column;
    align-items: flex-start;
  }
  .locandina {
    width: 60px;
    height: 90px;
  }
  h1 {
    font-size: 16px;
  }
  .movie_desc {
    font-size: 12px;
  }
}
