.app-container {
  background: linear-gradient(135deg, #2c3e50, #34495e); /* Dark, moody gradient */
  min-height: 90vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0.6rem;
  box-sizing: border-box;
}
.framer > h2{
  text-align:center
}

.window {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: #ffffffcc; /* Slightly transparent white for a modern look */;
  border-radius: 15px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
  padding: 40px;
  width: 90%;
  max-width: 800px;
  backdrop-filter: blur(10px); /* Adds a sleek blur effect */;
  border: 2px solid rgba(255, 255, 255, 0.5);
}
.embark-footer > * {
  background: #fcd535;
  border: none;
  border-radius: 25px;
  padding: 15px 30px; /* Slightly larger padding for better clickability */
  font-size: 1.2rem; /* Larger font for emphasis */
  font-weight: bold; /* Bold text for impact */
  color: #fff;
  cursor: pointer;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2); /* Slightly deeper shadow for depth */
  transition: transform 0.3s ease, box-shadow 0.3s ease, background 0.3s ease;
  text-align: center;
  animation: jump 1s ease-in-out infinite; /* Adding the jump animation */
}

@keyframes jump {
  0%, 100% {
    transform: translateY(0); /* Start and end at the normal position */
  }
  50% {
    transform: translateY(-10px); /* Move up by 10px */
  }
}

.embark-footer > *:hover {
  transform: scale(1.05); /* Slightly enlarges button on hover */
  box-shadow: 0 6px 15px rgba(0, 0, 0, 0.3); /* Enhanced shadow for hover effect */
  background: linear-gradient(135deg, #f2317e, #861762); /* Reverse gradient for hover state */
}

.embark-footer > *:active {
  transform: scale(0.98); /* Pressed button effect */
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.2);
}

.slogans {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 20px; /* Adds spacing between items */
  justify-content: center; /* Ensures alignment is centered */
  margin: 0 auto; /* Centers the container */
  padding: 20px; /* Adds padding around the container */
}

.slogans > p {
  background: linear-gradient(135deg, #10bab5, #ff76ce); /* Vibrant gradient */
  color: #fff;
  text-align: center;
  padding: 1.5rem; /* Adjust padding for better spacing */
  border-radius: 15px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  flex: 1 1 calc(30% - 20px); /* Flexible width, accounting for gaps */
  min-width: 200px; /* Ensures the items don't shrink too much */
  max-width: 400px; /* Limits the width for better readability */
  transition: transform 0.3s ease, box-shadow 0.3s ease; /* Adds a smooth hover effect */
  opacity: 0.9;
}

.slogans > p:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.3); /* Enhanced hover effect */
}

@media (max-width: 768px) {
  .slogans {
    flex-direction: column; /* Switch to column layout for smaller screens */
  }

  .slogans > p {
    flex: 1 1 100%; /* Full width for each item */
    margin-bottom: 20px; /* Adds spacing between items */
  }
}

  /* Styling for the passage */
/* Main container with flexbox */
.reading-use-of-english-grid {
  display: flex;
  flex-wrap: wrap; /* Allow items to wrap to the next row */
  gap: 1rem; /* Space between items */
  padding: 1rem;
  line-height: 1.8; /* Slightly larger line height for readability */
  align-items: flex-start; /* Align items to the start of the container */
  justify-content: flex-start; /* Align items at the start of the row */
  background: linear-gradient(135deg, #f0f8ff, #e0e4e9); /* Soft gradient background */
  border-radius: 15px; /* Rounded corners */
}

/* Styling for individual grid items */
.reading-use-of-english-grid > * {
  background-color: #ffffff;
  padding: 20px;
  border-radius: 12px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1); /* Soft shadow */
  font-size: 1rem;
  color: #333;
  transition: transform 0.3s ease, box-shadow 0.3s ease; /* Smooth transitions */
  flex: 1 1 300px; /* Flex item that grows and shrinks, minimum width of 300px */
  text-align: justify; /* Justified text for neat appearance */
}

.reading-use-of-english-grid > *:hover {
  transform: translateY(-5px); /* Slight hover effect */
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.15); /* Enhanced shadow */
}

/* Full-width row break */
.break {
  flex: 1 1 100%; /* Forces the element to take up the full row */
  margin: 0; /* Remove any default margin */
  padding: 0; /* Optional: Remove padding to avoid extra space */
}

/* Heading style */
.reading-use-of-english-grid h2 {
  font-size: 1.6rem;
  font-weight: bold;
  color: #2c3e50;
}

/* Paragraph text styling */
.reading-use-of-english-grid p {
  font-family: 'Open Sans', sans-serif;
  font-size: 1.1rem;
  color: #444;
  margin: 0;
  padding: 10px 0;
}

/* Link Styling */
.reading-use-of-english-grid a {
  color: #10bab5;
  text-decoration: none;
  font-weight: bold;
  transition: color 0.2s ease;
}

.reading-use-of-english-grid a:hover {
  color: #ff76ce;
}

