/* General Styles */
html {
  box-sizing: border-box;
  font-family: 'Open Sans', sans-serif;
}

*, *:before, *:after {
  box-sizing: inherit;
}

/* Background */
.background {
  display: flex;
  justify-content: center;
  padding: 0 25px 25px;
  position: relative;
  width: 100%;
  max-height: 60vh;
  background: linear-gradient(to bottom, #60a9ff 0%, #4394f4 100%);
}

.background::after {
  
  height: 350px;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 1;
}

@media (min-width: 900px) {
  .background {
    padding: 0 0 25px;
  }
}

/* Container and Panel */
.container-price {
  margin: 0 auto;
  padding: 0;
  max-width: 960px;
  width: 100%;
  transform: translateY(10%);
}

.panel {
  border-radius: 10px;
  position: relative;
  width: 100%;
  z-index: 10;
}

/* Pricing Table */
.pricing-table {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

@media (min-width: 900px) {
  .pricing-table {
    flex-direction: row;
  }
}

.pricing-table * {
  text-align: center;
  text-transform: uppercase;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-content: center;
}
.pricing-description * {
  text-transform:capitalize!important
}

.pricing-plan {
  border-bottom: 1px solid #e1f1ff;
  padding: 25px;
}

.pricing-plan:last-child {
  border-bottom: none;
}

@media (min-width: 900px) {
  .pricing-plan {
    border-bottom: none;
    border-right: 1px solid #e1f1ff;
    flex-basis: 100%;
    padding: 25px 50px;
  }

  .pricing-plan:last-child {
    border-right: none;
  }
}

.pricing-img {
  margin-bottom: 25px;
  max-width: 100%;
}

.pricing-header {
  color: #888;
  font-weight: 600;
  letter-spacing: 1px;
}

.pricing-features {
  color: #016FF9;
  font-weight: 600;
  letter-spacing: 1px;
  margin: 50px 0 25px;
}

.pricing-features-item {
  border-top: 1px solid #e1f1ff;
  font-size: 12px;
  line-height: 1.5;
  padding: 15px 0;
}

.pricing-features-item:last-child {
  border-bottom: 1px solid #e1f1ff;
}

.pricing-price {
  color: #016FF9;
  display: block;
  font-size: 32px;
  font-weight: 700;
}

.pricing-button {
  width: auto;
  border: 1px solid #9dd1ff;
  border-radius: 10px;
  color: #348EFE;
  display: inline-block;
  margin: 25px 0;
  padding: 15px 35px;
  text-decoration: none;
  transition: all 150ms ease-in-out;
}

.pricing-button:hover,
.pricing-button:focus {
  background-color: #e1f1ff;
}

.pricing-button.is-featured {
  background-color: #48aaff;
  color: #fff;
}

.pricing-button.is-featured:hover,
.pricing-button.is-featured:active {
  background-color: #269aff;
}

/* Tier Navigation */
.tier-navigation {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}

/* Container for the navigation buttons */
.radio-inputs {
  position: relative;
  display: flex;
  flex-wrap: wrap; /* Allow wrapping to the next line on smaller screens */

  justify-content: space-evenly;
  border-radius: 0.5rem;
  background-color: #70c489;
  box-sizing: border-box;
  font-size: 16px;
  width: 100%;
  padding: 1rem 1rem 0 1rem;
  margin: 0 auto; /* Center the navigation container */
  align-items: flex-end
}

/* Hide the actual radio inputs */
.radio-inputs .radio input {
  display: none;
}

/* Style for each navigation button */
.radio-inputs .radio .name {
  display: flex;
  cursor: pointer;
  align-items: center;
  justify-content: center;
  border-top-left-radius: 0.5rem;
  border-top-right-radius: 0.5rem;
  border: none;
  padding: 0.5rem 0.8rem;
  color: #fff; /* Text color for buttons */
  transition: all 0.15s ease-in-out;
  position: relative;
  background-color: #70c489; /* Default background color */
}

/* Style for the active button */
.radio-inputs .radio input:checked + .name {
  background-color: #e8e8e8;
  font-weight: 600;
  color: #1d1d29; /* Text color when active */
}

/* Hover effect for buttons */
.radio-inputs .radio input + .name:hover {
  color: #fff;
}

/* Hover effect for active buttons */
.radio-inputs .radio input:checked + .name:hover {
  color: #1d1d29;
}

/* Additional visual effects for active buttons */
.radio-inputs .radio input:checked + .name::after,
.radio-inputs .radio input:checked + .name::before {
  content: "";
  position: absolute;
  width: 10px;
  height: 10px;
  background-color: #70c489;
  bottom: 0;
}

.radio-inputs .radio input:checked + .name::after {
  right: -10px;
  border-bottom-left-radius: 300px;
  box-shadow: -3px 3px 0px 3px #e8e8e8;
}

.radio-inputs .radio input:checked + .name::before {
  left: -10px;
  border-bottom-right-radius: 300px;
  box-shadow: 3px 3px 0px 3px #e8e8e8;
}

.radio label{
  display: flex; 
  flex-direction: column; 
  text-align:center;
  gap: 2px;
}
.detItem { 
  color: #fcd535; 
  font: 11px Merriweather, sans-serif; 
}
.subscription-header{
  text-align: center;
}
.pricing-category{
  border: 0.5px solid silver;
  border-radius: 8px;
  background-color: white;
  flex-basis: 33%;
  padding-top: 1rem;
  margin-top: 1rem;
  box-shadow: 0 10px 13px -6px rgba(0, 0, 0, 0.08), 0 20px 31px 3px rgba(0, 0, 0, 0.09), 0 8px 20px 7px rgba(0, 0, 0, 0.02);
}
.info{
  text-align: center;
    padding: 0.3rem 0;
}
.user-subscription{
  margin-bottom: 2rem;
}
.gap{
  margin-top: 2rem;
}

.pricing-category:hover{
  transform: scale(1.05);
}
.category-header{
  display: flex;
  flex-direction: row;
  gap: 0.5rem;
  align-items: center;
  justify-content: center;
  font-size: 24px;
  font-weight: 800;
}
.category-header > img{
  height: 24px;
}
.pricing-description > li{
  display: flex;
  flex-direction: row;
  margin: 0.3rem 0;
  padding: 0;
  font-size: 12px;
  justify-content: flex-start;
}
.pricing-description li > img{
  height: 24px;
  width: fit-content;
  margin-right: 0.3rem;
}
.pricing-description li > span{
  text-align: left;
}
.starter{
  background-color: #FDFFC2
}
.greener{
  background-color: #94FFD8;
}
.purpler{
  background-color: #ff76cea6
}
.caution{
  display: flex;
  flex-direction: row;  
  font-size: 28px;
  justify-content: center;
  align-items: center;
}
.notify{
  padding: 1rem;
  border-radius: 0.375rem;
  font-size: 0.875rem;
  line-height: 1.25rem;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  background-color: #FEFFAC;
}
.pricing-list{
  width: 100%;

}

@media (max-width: 768px) {
  .radio-inputs {
    flex-wrap: wrap; /* Wrap buttons to the next line */
    padding: 1rem 0.5rem;
    gap: 0.5rem; /* Reduce spacing for smaller screens */
  }

  .radio-inputs .radio .name {
    font-size: 14px; /* Adjust font size */
    padding: 0.4rem 0.6rem; /* Adjust padding */
    flex: 1 1 40%; /* Buttons take up 40% of the container */
  }
  .radio {
    position: relative;
    display: inline-block;
    text-align: center;
    border-radius: 0.5rem;
    overflow: hidden;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    transition: all 0.3s ease;
  }
  .radio .name {
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    padding: 0.6rem 1rem;
    background-color: #70c489;
    border-radius: 0.5rem;
    color: rgba(255, 255, 255, 0.597);
    font-size: 16px;
    font-weight: 500;
    text-transform: capitalize;
    box-shadow: inset 0 0 0 2px transparent, 0 4px 8px rgba(0, 0, 0, 0.2);
    transition: all 0.3s ease;
  }  
}

/* Media Query for Very Small Screens */
@media (max-width: 480px) {
  .radio-inputs .radio .name {
    font-size: 12px; /* Reduce font size further */
    padding: 0.3rem 0.5rem; /* Reduce padding */
    flex: 1 1 100%; /* Each button takes full width */
  }
}